import React from "react";
const supplies = [
  { item: "Water", quantity: "100 liters", available: true },
  { item: "First Aid Kit", quantity: "25 kits", available: true },
  { item: "Batteries", quantity: "50 packs", available: false }
];
const Supplies = () => (
  <div>
    <div className="card">
      <h2>Essential Supplies</h2>
      <ul>
        {supplies.map((s, idx) => (
          <li key={idx}>
            {s.item}  {s.quantity}  
            <button className={s.available?"btn":"btn disabled"} disabled={!s.available}>
              {s.available ? "Reserve" : "Unavailable"}
            </button>
          </li>
        ))}
      </ul>
    </div>
  </div>
);
export default Supplies;
