import React from "react";
const shelters = [
  { name: "Community Center Nainital", capacity: 200, occupied: 45 },
  { name: "Govt School Bhowali", capacity: 150, occupied: 30 }
];
const Shelters = () => (
  <div>
    <div className="card">
      <h2>Available Shelters (Broadcast Mode)</h2>
      <ul>
        {shelters.map((s, idx) => (
          <li key={idx}>
            {s.name}  {s.occupied}/{s.capacity} occupied
            <span className={s.occupied/s.capacity<.8?"status-green":"status-yellow"}></span>
          </li>
        ))}
      </ul>
    </div>
  </div>
);
export default Shelters;
